import axios from 'axios';
import { appId } from 'config'
import noData from '@/components/noData'
import back from '@/components/back'
    export default {
        data(){
            return{
                span:2,
                list:[],
			    temp:true,
			    idx:0,
			    moreList:null,
			    flag:false,
			    page:1,
			    patient: this.$store.state.patient,
			    patientName:"",
            }
        },
        components: {
            noData,
            back
        },
        beforeDestroy(){
	        window.onscroll = null
	    },
        mounted:function(){
	    	var scroller=1;
			var Total='';
			const _this=this;
			document.title = this.$route.meta.title;
            window.onscroll = function(){  //监听滚动轴，
            // console.log(Total)
				var scrollTop = document.documentElement.scrollTop||document.body.scrollTop;
				var windowHeight = document.documentElement.clientHeight || document.body.clientHeight;
				var scrollHeight = document.documentElement.scrollHeight||document.body.scrollHeight;
				if(scrollTop+windowHeight==scrollHeight){
					
					if(Total/10>scroller && this.idx > 0){
						scroller=1+scroller
					this.scroller=scroller;   //传页数给后台
                    _this.fn(scroller)
                    // console.log(Total)
					}
					
				}
				
			}
	    },
        created() {
        	this.fn(1)
        	this.patientName = this.patient.PatientName || ""
        	console.log(this.patient)
        },
        methods:{
        	fn(sss){
            	if(!this.temp){
            		return
            	}
            	this.flag = false
            	this.page = sss
                this.$post('Prescription/Prescription/GetPrescriptionListByPatientId',{
	                ApplicantId:this.patient.AccountId,
	                PatientId:this.patient.PatientId,
	                PageIndex:sss,
					PageSize:10,
	               }).then(res=>{
	                if(res.ReturnData.length!=0){
	                    this.list=this.list.concat(res.ReturnData);
	                }
	                this.moreList = res.ReturnData || []
	                this.idx ++;
	                this.flag = true
	            })  
            }
        }
    }



